h2 {
	@include museo700;
	color: $c_content;
	font-size: 36px;

	@include max-screen (376px) {
		font-size: 30px;
	}

	@include max-screen (321px) {
		font-size: 28px;
	}
}

.home {

	.head-content-nav {
		border-bottom: 0;
	}

	// TODO Слайдер скидок на главной

	.slider-sales {
		.slick-slide {
			border: none;
			outline: none;

			&:focus,
			&:active {
				border: none;
				outline: none;
			}
		}

		.slick-arrow {
			width: 35px;
			height: 35px;
			border-radius: 50%;
			background: $c_bg_btn;
			transition: all 0.3s linear;
			cursor: pointer;

			&:hover {
				background: $c_bg_btn_hover;
			}

			&.custom-slick-arrow {
				display: flex;
				justify-content: center;
				align-items: center;
				position: absolute;
				right: 15px;
				top: -84px;

				span {
					display: block;
					width: 12px;
					height: 12px;
					border-right: 1px solid white;
					border-bottom: 1px solid white;
				}

				&--prev {
					right: 65px;

					span {
						transform: translateX(3px) rotate(135deg);
					}
				}

				&--next {
					span {
						transform: translateX(-3px) rotate(-45deg);
					}

				}
			}
		}

		.slick-dots {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 100%;

			li {
				width: 15px;
				height: 15px;
				margin: 5px;
				border-radius: 50%;
				display: block;
				border: 2px solid $c_func_color;

				button {
					font-size: 0;
					height: 0;
					width: 0;
					border: none;
					background: transparent;
				}

				&.slick-active {
					background: $c_func_color;
				}
			}
		}
	}


}

.home-action {
	background-color: $c_white;
	padding-top: 80px;

	// padding-bottom: 90px;
	@include max-screen (321px) {
		padding-top: 0;
	}

}

.action-main__title {
	margin-bottom: 40px;
}

.head-content-block {
	background-image: url(../images/home/head-BG.png);
	background-size: contain;
	background-repeat: no-repeat;
	padding: 275px 0 235px 0;
	margin-top: -100px;
	background-position: center;

	@include max-screen (1600px) {
		background-position-x: center;
	}

	@include max-screen (1200px) {
		padding-bottom: 100px;
		background-size: cover;
	}

	@include max-screen (768px) {
		background-position-x: -75px;
	}

	@include max-screen (426px) {
		background-position: right -500px top -40px;
	}

	@include max-screen (376px) {
		background-position: right -550px top -25px;
	}

	@include max-screen (321px) {
		background-position: right -655px top -70px;
	}

	a,
	i {
		color: $c_content;
	}

	.head-content-nav {
		display: flex;
		justify-content: space-between;
		align-items: center;
		max-width: 1170px;
		width: 100%;
		padding-bottom: 200px;

		@include max-screen (1200px) {
			padding-bottom: 100px;
		}

		@include max-screen (992px) {
			flex-wrap: wrap;
		}

		@include max-screen (768px) {
			padding-bottom: 50px;
		}

		.logo-block {
			width: 170px;
			height: 45px;
			margin-top: -5px;

			img {
				width: 100%;
				height: 100%;
			}

			@include max-screen (768px) {
				margin-top: 0;
				margin-bottom: 30px;
			}

			@include max-screen (426px) {
				margin-bottom: 20px;
			}
		}

		.head-nav-list {
			display: flex;
			justify-content: space-between;
			padding-left: 35px;

			@include max-screen (1170px) {
				padding-left: 5px;
			}

			@include max-screen (992px) {
				order: 1;
				width: 100%;
				margin-top: 20px;
			}

			@include max-screen (768px) {
				display: none;
			}

			li {
				margin-right: 45px;

				@include max-screen (1170px) {
					margin-right: 10px;
				}

				@include max-screen (992px) {
					margin-right: 0;
				}

				a {
					font-size: 15px;
					@include museo500;

					&:hover,
					&:focus {
						color: $c_func_color;
					}

					@include max-screen (992px) {
						font-size: 20px;
					}
				}
			}
		}

		.phone-basket-block {
			display: flex;
			justify-content: space-between;
			align-items: center;
			font-size: 18px;
			@include museo500;

			@include max-screen (321px) {
				flex-wrap: wrap;
			}

			.phone-item {
				display: flex;
				align-items: center;
				padding-right: 50px;

				&:hover,
				&:focus {
					color: $c_func_color;
				}

				@include max-screen (1170px) {
					padding-right: 30px;
				}

				@include max-screen (376px) {
					padding-right: 20px;
					font-size: 15px;
				}

				@include max-screen (321px) {
					margin-bottom: 20px;

				}

				i {
					display: flex;
					justify-content: center;
					align-items: center;
					width: 45px;
					height: 45px;
					background-color: #fff;
					border-radius: 10px;
					margin-right: 15px;

					&::before {
						font-size: 24px;
					}
				}
			}

			.basket-item {
				display: flex;
				align-items: center;

				&:hover,
				&:focus:after {
					color: $c_func_color;
				}

				@include max-screen (376px) {
					font-size: 15px;
				}

				i {
					display: flex;
					justify-content: center;
					align-items: center;
					width: 45px;
					height: 45px;
					background-color: #fff;
					border-radius: 10px;
					margin-right: 15px;
					position: relative;

					&:after {
						content: "99";
						position: absolute;
						display: flex;
						padding-top: 2px;
						padding-left: 1px;
						justify-content: center;
						align-items: center;
						top: -8px;
						right: -6px;
						width: 20px;
						height: 20px;
						background-color: $c_func_color;
						border-radius: 50%;
						@include museo500;
						font-size: 10px;
						color: #fff;
					}

					&::before {
						font-size: 24px;
					}
				}
			}
		}
	}

	.head-title-block {

		@include max-screen (1170px) {
			width: 100%;
		}

		h1 {
			// max-width: 650px;
			@include museo100;

			line-height: 58px;
			font-size: 48px;
			color: $c_content;
			margin-bottom: 45px;

			strong {
				@include museo700;
			}

			@include max-screen (1024px) {
				font-size: 42px;
			}

			@include max-screen (768px) {
				font-size: 40px;
			}

			@include max-screen (426px) {
				line-height: 48px;
			}

			@include max-screen (321px) {
				font-size: 36px;
				line-height: 40px;
			}

		}

		.goto-catalog {

			@include museo500;
			font-size: 16px;
			color: #fff;
			display: flex;
			justify-content: center;
			align-items: center;
			width: 240px;
			height: 60px;
			border-radius: 50px;
			text-align: center;
			background: $c_bg_btn;
			position: relative;
			z-index: 10;

			&:after {
				@include psev_full;
				border-radius: 50px;
				background: $c_bg_btn_hover;
				opacity: 0;
				z-index: -1;
			}

			&:hover:after,
			&:focus:after {
				opacity: 1;

			}
		}
	}
}

.home-catalog {
	@include museo700;
	color: $c_content;
	padding-top: 90px;
	background-color: $c_white;
	position: relative;
	z-index: 1;

	@include max-screen (768px) {
		padding-top: 50px;
	}

	@include max-screen (426px) {
		padding-top: 30px;
	}

	@include max-screen (321px) {
		// padding-top: 0;
	}

	h2 {
		padding-bottom: 50px;
	}

	.home-catalog-content {
		&__top {
			display: flex;
			justify-content: space-between;
			margin-bottom: 25px;

			@include max-screen (768px) {
				flex-direction: column;
				justify-content: center;
				align-items: center;
				margin-bottom: 0;
			}

		}

		&__column {
			width: 50%;

			@include max-screen (768px) {
				width: 100%;
			}


			&:last-child {
				margin-left: 30px;

				@include max-screen (768px) {
					margin-left: 0;
				}

			}

			@include max-screen (768px) {
				margin-bottom: 30px;
			}

		}

		&__item {
			display: flex;
			justify-content: flex-start;
			align-items: center;
			background-size: 100%;
			background-repeat: no-repeat;
			height: 335px;
			border-radius: 6px;

			@include max-screen (1200px) {
				background-size: cover;
				background-position: left top 30px;
			}

			@include max-screen (768px) {
				height: auto;
				// height: 250px;
				background-position: left top;


			}



		}

		&__item-text {
			display: flex;
			flex-direction: column;
			max-width: 200px;
			padding: 0 15px;

			@include max-screen (768px) {
				padding: 120px 15px;
			}

			@include max-screen (480px) {
				padding: 60px 16px;
			}


			h5 {
				font-size: 30px;
				line-height: 58px;
				@include museo700;
				color: $c_content;


			}

			p {
				font-size: 16px;
				line-height: 24px;
				@include museo300;
				color: $c_content;
			}


		}

		.content-bottom {
			display: grid;
			grid-template-columns: repeat(4, 1fr);
			grid-gap: 30px;
			margin-bottom: 60px;

			@include max-screen (768px) {
				grid-template-columns: repeat(2, 1fr);
				grid-gap: 20px;
			}

			@include max-screen (426px) {
				grid-gap: 10px;
			}


			&-item {
				position: relative;
			}

			h6 {
				position: absolute;
				max-width: 120px;
				left: 20px;
				top: 50%;
				transform: translateY(-50%);
				font-size: 18px;
				@include museo700;
				color: $c_content;

				@include max-screen (426px) {
					font-size: 14px;
					line-height: 16px;
					max-width: 100px;
					left: 10px;
				}

			}
		}
	}

	.goto-catalog {
		@include museo500;
		margin: 0 auto;
		font-size: 16px;
		color: #fff;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 270px;
		height: 60px;
		border-radius: 50px;
		text-align: center;
		background: $c_bg_btn;
		transition: background .7s ease;
		position: relative;
		z-index: 10;

		&:after {
			@include psev_full;
			border-radius: 50px;
			background: $c_bg_btn_hover;
			opacity: 0;
			z-index: -1;
		}

		&:hover:after,
		&:focus:after {
			opacity: 1;

		}
	}
}

// Третий блок (О магазине)

.home-about {
	padding-bottom: 148px;
	padding-top: 80px;
	background-color: $c_white;

	@include max-screen (426px) {
		padding-bottom: 70px;
	}

	@include max-screen (321px) {
		padding-top: 60px;
	}

	.about-content {
		display: flex;
		justify-content: space-between;

		@include max-screen (992px) {
			flex-wrap: wrap;
		}

		.about-content-left {
			flex-basis: 50%;

			@include max-screen (992px) {
				flex-basis: 100%;
			}

			h2 {
				margin-bottom: 30px;
			}

			p {
				@include museo500;
				font-size: 16px;
				padding-right: 30px;
				color: $c_content;
				letter-spacing: 0.24px;

				@include max-screen (992px) {
					padding-right: 0;
				}
			}
		}

		.about-content-right {
			flex-basis: 50%;

			@include max-screen (992px) {
				flex-basis: 100%;
			}

			img {
				margin-top: -60px;

				@include max-screen (1170px) {
					margin-top: 0;
				}
			}
		}
	}
}

.home-news {
	background-color: $c_white;
	padding-bottom: 40px;

	h2 {
		margin-bottom: 50px;
	}

	.home-news-content {
		display: flex;
		justify-content: space-between;

		@include max-screen (768px) {
			flex-wrap: wrap;
			width: 100%;
		}

		.news-content-item {
			margin-right: 30px;
			position: relative;

			&:hover h5,
			&:focus h5 {
				color: $c_func_color;
			}

			&:last-child {
				margin-right: 0;
			}

			@include max-screen (768px) {
				margin-bottom: 40px;
				width: 100%;
				margin-right: 0;
			}

			img {
				@include max-screen (768px) {
					width: 100%;

				}
			}

			h5 {
				@include museo700;
				font-size: 16px;
				color: $c_content;
				width: 100%;
				border-bottom: 1px solid #eef5f9;
				padding-bottom: 15px;
				padding-top: 15px;

				@include max-screen (1170px) {
					@include museo500;
				}

				@include max-screen (992px) {
					height: 35%;
				}

				@include max-screen (768px) {
					height: 20%;
				}
			}

			.datanews {
				position: absolute;
				top: 15px;
				right: 15px;
				height: 24px;
				width: 95px;
				display: flex;
				justify-content: center;
				align-items: center;
				@include museo700;
				color: #fff;
				font-size: 13px;
				background-color: #70c4fe;
				border-radius: 3px;

			}
		}
	}
}

.home-masterclass {
