	color: $c_content;

	@include max-screen (1500px) {
		background-position: left -300px top;
	}

	@include max-screen (1170px) {
		background-position: left -450px top;
	}

	@include max-screen (1024px) {
		background-position: left -500px top;
	}

	@include max-screen (768px) {
		background-image: url(../images/home/list2.png);
		background-position: right top 30px;
		background-size: 200px;
		background-color: #ecf2f8;
	}

	@include max-screen (426px) {
		background-position: right bottom;
		background-size: 150px;
	}

	@include max-screen (321px) {
		background-size: 150px;
		background-position: right bottom 20px;
	}

	.master-class-content {
		padding-top: 200px;
		padding-bottom: 100px;

		@include max-screen (426px) {
			padding-top: 50px;
			padding-bottom: 50px;
		}

		h2 {
			margin-bottom: 30px;
		}

		p {
			max-width: 500px;
			letter-spacing: 0.24px;
			@include museo500;
			margin-bottom: 50px;
		}

		.goto-mc {
			@include museo500;
			font-size: 16px;
			color: #fff;
			display: flex;
			justify-content: center;
			align-items: center;
			width: 180px;
			height: 60px;
			border-radius: 50px;
			text-align: center;
			background: $c_bg_btn;
			transition: background .7s ease;
			position: relative;
			z-index: 10;

			&:after {
				@include psev_full;
				border-radius: 50px;
				background: $c_bg_btn_hover;
				opacity: 0;
				z-index: -1;
			}

			&:hover:after,
			&:focus:after {
				opacity: 1;

			}
		}
	}
}

// Шестой блок (Хиты продаж)

.home-bestsellers,
.milovar-catalog .catalog-full .catalog-right {
	@include museo500;
	color: $c_content;
	padding-bottom: 100px;

	@include max-screen (376px) {
		padding-bottom: 70px;
	}

	h2 {
		margin-bottom: 45px;
		margin-top: 90px;

		@include max-screen (426px) {
			margin-top: 70px;
		}
	}

	.categ {
		@include flex($j-sb);

		@include max-screen (992px) {
			flex-wrap: wrap;
		}

		.best-content-item {
			box-shadow: 0px 5px 6px 0px rgba(31, 64, 91, 0.05);
			max-width: 270px;

			@include max-screen (1170px) {
				max-width: 200px;
			}

			@include max-screen (992px) {
				max-width: 50%;
				margin-bottom: 30px;
			}

			@include max-screen (768px) {
				margin-right: 10px;
				margin-left: 10px;
				max-width: 45%;
			}

			@include max-screen (426px) {
				max-width: 100%;
				width: 100%;
			}

			.best-top-content {
				width: 270px;
				height: 240px;
				background-color: #eef5f9;
				border-radius: 5px;
				@include flex;

				@include max-screen (1170px) {
					width: 100%;
				}
			}

			.best-bottom-content {
				padding: 25px;

				h5 {
					max-width: 220px;
					font-size: 15px;
					letter-spacing: 0.24px;
					margin-bottom: 20px;
				}

				.bottom-func {
					@include flex($j-sb, $a-ic, $fww);

					@include max-screen (1170px) {
						flex-direction: column;
						align-items: center;
					}

					span.fasov {
						@include museo500;
						flex-basis: 100%;
						font-size: 13px;
						color: #949daf;
						margin-bottom: 5px;
						position: relative;

						&:after {
							@include psev;
							background-image: url(../images/home/arr.png);
							background-repeat: no-repeat;
							left: 83%;
							top: 225%;
							z-index: 2;
							cursor: pointer;
							pointer-events: none;

							@include max-screen (1170px) {
								left: 125%;
							}

							@include max-screen (992px) {
								left: 160%;
							}
						}

					}

					.fas-select {
						flex-basis: 100%;
						display: block;
						padding: 10px;
						max-width: 100%;
						border-radius: 5px;
						-moz-appearance: none;
						-webkit-appearance: none;
						appearance: none;
						border: 1px solid #dde0e6;
						@include museo700;
						font-size: 13px;
						color: $c_content;
						position: relative;
						z-index: 1;
						cursor: pointer;

						@include max-screen (1170px) {
							padding-right: 40px;
						}

						&:focus {
							outline: none;
						}
					}

					.colvo {
						margin-top: 20px;
						outline: none;
						border: none;

						input[type=number]::-webkit-inner-spin-button,
						input[type=number]::-webkit-outer-spin-button {
							-webkit-appearance: none;
							-moz-appearance: none;
							appearance: none;
							margin: 0;
						}

						input {
							text-align: center;
							@include museo700;
							font-size: 13px;
							border: none;
							color: #949daf;

							&:focus {
								outline: none;

							}
						}

						.quan-plus,
						.quan-minus {
							border-radius: 50%;
							border: none;
							width: 26px;
							height: 26px;
							background-color: #949daf;
							color: #fff;
							font-size: 16px;
							font-weight: 600;
							text-align: center;
							vertical-align: middle;
							cursor: pointer;

							&:focus {
								outline: none;

							}
						}
					}

					.into-basket {
						@include museo500;
						font-size: 14px;
						color: #fff;
						display: flex;
						justify-content: center;
						align-items: center;
						width: 115px;
						height: 35px;
						border-radius: 50px;
						text-align: center;
						background: $c_bg_btn;
						margin-top: 20px;
						transition: background .7s ease;
						position: relative;
						z-index: 10;

						&:after {
							@include psev_full;
							border-radius: 50px;
							background: $c_bg_btn_hover;
							opacity: 0;
							z-index: -1;
						}

						&:hover:after,
						&:focus:after {
							opacity: 1;

						}
					}
				}
			}
		}
	}
}